*,
*::before,
*::after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  font-family: "PT Sans", Helvetica, Arial, sans-serif;
  font-size: 18px;
  line-height: 1.2;

  --brand-color: #0c98d9;
}


body {
  min-height: 100vh;
  display: grid;
  grid-template-columns: auto;
  grid-template-rows: min-content auto min-content;
  grid-template-areas:
    "header"
    "main"
    "footer";
}

body > header {
  grid-area: header;
}

body > main {
  grid-area: main;
}

body > footer {
  grid-area: footer;
}


.container {
  width: auto;
  max-width: 960px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 0;
  padding-right: 0;
}

.container--wide {
  max-width: 1020px;
}

